import { Injectable } from '@angular/core';
import { Cards, Envelopes, Cardonly } from './dati';
import { Card } from './dati';
import { CardSelected } from './selectcard';
import { HttpClient } from '@angular/common/http';
import { AuthService } from './auth.service';

import {environment} from '../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class DatiService {
  card: Card = null;
  dati_carte: Array<Cards> = [];
  dati_carte_utente: Cards;
  carta_selezionata: CardSelected = null;
  buste_espansione: Envelopes;
  single_card: Card;
  card_only: Cardonly;

  quantity: Array<number> = [this.dati_carte.length];
  typedeck: Array<string> = [];

  // input POST card
  inputcod: string;
  inputnome: string;
  inputattributo: string;
  inputlivello: number;
  inputnumero: number;
  inputdescrizione: string;
  inputattacco: number;
  inputdifesa: number;
  inputtipo: string;
  inputblugem: number;
  inputredgem: number;
  inputpendeffect: string;
  inputimg: string;

  // input POST envelope
  inputenvelopename: string;
  inputenvelopeyear: number;

  // input post cards in envelope
  inputcodcard: string;
  inputcodenvelope: number;

  constructor(private http: HttpClient, public authservice: AuthService) { }

  Load_All_Cards(){
    let url = environment.url+"/allcards";
    this.http.get(url).subscribe((data: Array<Cards>) => {
      this.dati_carte = data;
    });
  }

  AddToDeck(card: Card, index: number){
    this.card = card;
    this.carta_selezionata = {
      userid_fk: localStorage.getItem("token_decoded_userid"),
      cod_fk: this.card.cod,
      qnt: this.quantity[index],
      type: this.typedeck[index],
    };
    let url = environment.url+"/users/"+localStorage.getItem("token_decoded_userid")+"/cards/"+this.card.cod+"/type/"+this.carta_selezionata.type
    let url2 = environment.url+"/users/"+localStorage.getItem("token_decoded_userid")+"/cards/"+this.card.cod
    let url3 = environment.url+"/users/"+localStorage.getItem("token_decoded_userid")+"/cards/"+this.card.cod+"/type/"+this.carta_selezionata.type
    if(this.carta_selezionata.qnt != null && this.carta_selezionata.type != null){
      this.http.get(url).subscribe((data: any) =>{ // modifica
        console.log(data.card.cod_fk);
        if(data.card.cod_fk == this.carta_selezionata.cod_fk && data.card.type == this.carta_selezionata.type){
          this.http.put(url3, this.carta_selezionata).subscribe((data:any) => {
            console.log(data);
          });
          this.dati_carte_utente["cards"].splice(index, 1);
          this.dati_carte_utente["cards"].push(card)
        }else if(data.card == "false"){
          this.http.post(url2, this.carta_selezionata).subscribe((data: any) => {
            alert("carta aggiunta con successo!")
          });
        }
      })
    }else{
      alert("ERRORE: devi aggiungere una quantit di carte da agiungere al deck e il tipo di deck");
    }

  }

  getCard(codice: string): Card{
    let url = environment.url+"/singlecard/"+codice;
    this.http.get(url).subscribe((data: Card) => {
      this.single_card = data;
    });
    return this.single_card
  }

  deleteCardfromdeck(card: Card, index: number){
    this.dati_carte_utente["cards"].splice(index, 1);
    this.card = card;
    let url = environment.url+"/users/"+localStorage.getItem("token_decoded_userid")+"/cards/"+this.card.cod+"/type/"+this.card.type
    this.http.delete(url).subscribe((data: any) => {
      console.info("Rimozione effetuata con successo");
    })
  }

  CardsLoggedUser(): any{
    let url = environment.url+"/users/"+localStorage.getItem("token_decoded_userid")+"/cards";
    return this.http.get(url).subscribe((data: Cards) => {
      this.dati_carte_utente = data;
      //let a = data.cards[0].type;
    });
  };

  ListofEnvelopes(){
    let url = environment.url+"/envelope/all";
    this.http.get(url).subscribe((data: Envelopes) => {
      this.buste_espansione = data;
    });
  };

  AddCardToList(){
    let url = environment.url+"/cards"
    let card = {};
    card["cod"] = this.inputcod;
    card["nome"] = this.inputnome;
    card["attributo"] = this.inputattributo;
    card["livello"] = this.inputlivello;
    card["numero"] = this.inputnumero;
    card["descrizione"] = this.inputdescrizione;
    card["attacco"] = this.inputattacco;
    card["difesa"] = this.inputdifesa;
    card["tipo"] = this.inputtipo;
    card["blugem"] = this.inputblugem;
    card["redgem"] = this.inputredgem;
    card["pendeffect"] = this.inputpendeffect;
    card["img"] = this.inputimg;

    this.http.post(url, card).subscribe((data: any) => {
      console.info(data);
    });
    alert("nuova carta aggiunta con successo!")
  }

  AddEnvelope(){
    let url = environment.url+"/envelope"
    let envelope = {};

    envelope["nome"] = this.inputenvelopename;
    envelope["anno"] = this.inputenvelopeyear;

    this.http.post(url, envelope).subscribe((data: any) => {
      console.info(data);
    })
    alert("nuova busta aggiunta con successo!")
  }

  AddCardToEnvelope(){
    let url = environment.url+"/cards/"+ this.inputcodcard + "/envelope/"+ this.inputcodenvelope;
    this.http.post(url, url).subscribe((data: any) => {})
    alert("Relazione aggiunta con successo!")
  }
}
