import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import {AuthService} from '../auth.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  form: FormGroup;
  constructor(public authservice: AuthService) { }

  ngOnInit() {
    this.form = new FormGroup({
      firstname: new FormControl(this.authservice.inputnome, Validators.required),
      lastname: new FormControl(this.authservice.inputcognome, Validators.required),
      email: new FormControl(this.authservice.inputemail, [Validators.required, Validators.email]),
      pass: new FormControl(this.authservice.inputpass, Validators.required),
  })
  this.form.valueChanges.subscribe(() => {
      this.authservice.inputnome= this.form.value.firstname;
      this.authservice.inputcognome = this.form.value.lastname;
      this.authservice.inputemail = this.form.value.email;
      this.authservice.inputpass = this.form.value.pass;
    });
}
}
