import { Component, OnInit } from '@angular/core';
import { RecipesService, Recipe } from '../recipes.service';
import {Router} from '@angular/router';
import { Card } from '../dati';

@Component({
  selector: 'app-recipes-list',
  templateUrl: './recipes-list.component.html',
  styleUrls: ['./recipes-list.component.css']
})
export class RecipesListComponent implements OnInit {

  constructor( public datarecipe: RecipesService,
               public route: Router,
               private router: Router, ) { }

  ngOnInit() {
    this.datarecipe.Getallallrecipes()
    this.datarecipe.Getrecipesidofuser();
  }

  showRecipeDetail(recipe: Recipe){
    this.datarecipe.single_recipe = recipe;
    this.route.navigate(['recipess', this.datarecipe.single_recipe.idric]);
  }

  deleterec(id: number, ind: number){
    this.datarecipe.DeleteRecipe(id, ind);
  }
}
