import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import {HomeComponent} from './home/home.component';
import {CardlistComponent} from './cardlist/cardlist.component';
import {RegisterComponent} from './register/register.component';
import {LoginComponent} from './login/login.component';
import {LoginpageComponent} from './loginpage/loginpage.component';
import {CarddetailComponent} from './carddetail/carddetail.component';
import {AdminpageComponent} from './adminpage/adminpage.component';

import {RecipesListComponent} from './recipes-list/recipes-list.component';
import {DuelistutilityComponent} from './duelistutility/duelistutility.component';
import {DeckDetailComponent} from './deck-detail/deck-detail.component';
import {RulespageComponent} from './rulespage/rulespage.component';

import { AuthGuard } from './auth.guard';

const routes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'cardlist', component: CardlistComponent, },
  { path: 'register', component: RegisterComponent, },
  { path: 'login', component: LoginComponent },
  { path: 'loginpage', component: LoginpageComponent, canActivate: [AuthGuard] },
  { path: 'cards/:id', component: CarddetailComponent, },
  { path: 'recipess/:id', component: DeckDetailComponent, },
  { path: 'adminpage', component: AdminpageComponent, canActivate: [AuthGuard] },
  { path: 'recipes', component: RecipesListComponent, },
  { path: 'rulespage', component: RulespageComponent, },
  { path: 'utility', component: DuelistutilityComponent,},
  { path: '**', redirectTo: '/home', pathMatch: 'full' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { relativeLinkResolution: 'legacy' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
