import { Injectable } from '@angular/core';
import { Users } from './users';
import { HttpClient } from '@angular/common/http';
import { JwtHelperService } from '@auth0/angular-jwt';

import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import {environment} from '../environments/environment';

@Injectable({
  providedIn: 'root'
})

export class AuthService {
  // input per la registrazione
  helper = new JwtHelperService();

  users: Users = null;

  inputnome: string;
  inputcognome: string;
  inputpass: string;
  inputemail: string;

  usersData: any;
  usersError: any;

  // input per il login
  inputlogemail: string;
  inputlogpass: string;
  datasend: Datalogin = null;

  loginData: any;
  loginError: any;

  constructor(private http: HttpClient) { }

  registeruser(){
    let url = environment.url+"/register";
    let users = {};
    users["nome"] = this.inputnome;
    users["cognome"] = this.inputcognome;
    users["pass"] = this.inputpass;
    users["email"] = this.inputemail;
    users["privilegi"] = "utente";

    this.http.post(url, users).subscribe((data: any) => {
      console.info(data);
      this.usersData = data;
      this.usersError = null;
    }, error => {
      this.usersError = error.error;
      this.usersData = null;
      console.error(this.usersError);
    });
  }

  login(email: string, password: string): Observable<boolean> {
      return this.http.post<{token: string}>(environment.url+'/login', {pass: password, email: email})
        .pipe(
          map(result => {
            localStorage.setItem('access_token', result.token);
            localStorage.setItem('token_decoded_userid', this.helper.decodeToken(localStorage.getItem('access_token')).userID)
            return true;
          })
        );
    }

    logout() {
      localStorage.removeItem('access_token');
      localStorage.removeItem('token_decoded_userid');
    }

    public get loggedIn(): boolean {
      return (localStorage.getItem('access_token') !== null);
    }

    public get adminControl(): boolean {
      if(this.helper.decodeToken(localStorage.getItem('access_token')).privileges == 'admin'){
        return true;
      } else {
        return false;
      }

    }

    public get UserLoggedName(): string{
      return this.helper.decodeToken(localStorage.getItem('access_token')).Nome
    }
    public get UserLoggedCognome(): string{
      return this.helper.decodeToken(localStorage.getItem('access_token')).Cognome
    }
    public get UserLoggedEmail(): string{
      return this.helper.decodeToken(localStorage.getItem('access_token')).Email
    }

}

export interface Datalogin{
  pass: string,
  email: string,
}
