import { Pipe, PipeTransform } from '@angular/core';
import {DatiService} from './dati.service';
import { Envelopes, Cards } from './dati';

@Pipe({
  name: 'envelopesFilter'
})
export class EnvelopesFilterPipe implements PipeTransform {

  transform(cardlist: any, packname: any): any {
    if(!packname) return cardlist;
      return cardlist.filter(function(card){
        for(let y = 0; y<card.espansioni.length; y++){ // errore
          if(card.espansioni[y].nome == packname){
            return card.nome;
          }
        }
    });
  }
}
