import { LeggendaryCardsComponent } from './leggendary-cards/leggendary-cards.component';
import { PdfViewerModule } from 'ng2-pdf-viewer';
// ...
export function tokenGetter() {
  return localStorage.getItem('access_token');
}

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    HomeComponent,
    CardlistComponent,
    RegisterComponent,
    LoginComponent,
    LoginpageComponent,
    AdminpageComponent,
    EnvelopesFilterPipe,
    CarddetailComponent,
    RulespageComponent,
    DuelistutilityComponent,
    FooterComponent,
    OtherfilterPipe,
    FieldErrorComponent,
    RecipesListComponent,
    DeckDetailComponent,
    LeggendaryCardsComponent
  ],
  imports: [
    BrowserModule.withServerTransition({ appId: 'serverApp' }),
    AppRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    FileUploadModule,
    PdfViewerModule,
    JwtModule.forRoot({
      config: {
        tokenGetter: tokenGetter,
        whitelistedDomains: ['localhost:8000'],
        blacklistedRoutes: ['localhost:8000/login']
      }
    })
  ],
  providers: [{provide: ErrorHandler, useClass: ErrorsService},
    AuthService,
    AuthGuard], //provvede alla gestione degli errori
  bootstrap: [AppComponent]
})
export class AppModule { }

registerLocaleData(localeIt, 'it')
