import { Component, OnInit } from '@angular/core';
import {DatiService} from '../dati.service';
import {AuthService} from '../auth.service';
import {RecipesService} from '../recipes.service';
import {Cards, Card} from '../dati';

@Component({
  selector: 'app-loginpage',
  templateUrl: './loginpage.component.html',
  styleUrls: ['./loginpage.component.css']
})
export class LoginpageComponent implements OnInit {
  name: string;
  surname: string;
  email: string;
  val: number = null;
  constructor(public carte: DatiService,
              public utente: AuthService,
              public datarecipe: RecipesService) {
                this.name = this.utente.UserLoggedName;
                this.surname = this.utente.UserLoggedCognome;
                this.email = this.utente.UserLoggedEmail;
              }

  ngOnInit() {
    this.carte.CardsLoggedUser();
  }

  delete(card: Card, ind: number): void {
    this.carte.deleteCardfromdeck(card, ind);
  }

}
