import { Component, OnInit } from '@angular/core';
import {EnvelopesFilterPipe} from '../envelopesfilter.pipe';
import {AuthService} from '../auth.service';
import {DatiService} from '../dati.service';
import {Cards, Card} from '../dati';
import {Envelopes} from '../dati';
import {Router} from '@angular/router';

@Component({
  selector: 'app-cardlist',
  templateUrl: './cardlist.component.html',
  styleUrls: ['./cardlist.component.css']
})
export class CardlistComponent implements OnInit {

  pack: string = "";
  constructor(public carte: DatiService,
              public pacchetti: DatiService,
              public route: Router,
              private router: Router,
              private auth: AuthService) { }

  ngOnInit() {
    this.carte.Load_All_Cards();
    this.pacchetti.ListofEnvelopes();
  }

  setPack(e: string){
    this.pack = e;
    console.log(e);
  }

  showCardDetail(card: Card){
    this.carte.single_card = card;
    this.route.navigate(['cards',this.carte.single_card.cod]);
  }
}
