import { Injectable } from '@angular/core';
import { Cards, Card } from './dati';
import { HttpClient } from '@angular/common/http';
import {AuthService} from './auth.service';

import {environment} from '../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class RecipesService {

  single_recipe: Recipe;
  singleric: Cards;

  /*maindeck: Array<Cards> = [];
  extradeck: Array<Cards> = [];
  sidedeck: Array<Cards> = [];*/

  allric: Recipes;
  all_comment: Comments;

  user_comment: Comment;

  all_comment_ids: allidcomment;

  common_id_comment: idcomment;

  recipesid: Array<allidrecipe> = [];

  constructor(private http: HttpClient, public utente: AuthService) { }

  Addrecipes(name: string, desc: string): void{
    name = name.trim();
    desc = desc.trim();

    if (!name) { return; }
    if (!desc) { return; }
    let url = environment.url+"/ricetta/user/"+localStorage.getItem("token_decoded_userid")
    let recipe = {}

    recipe["nomeric"] = name;
    recipe["desc"] = desc;

    this.http.post(url, recipe).subscribe((data: any) => {
        alert(data.status)
    });

  }

  Getrecipe(id: number): Cards{
    let url = environment.url+"/ricetta/"+id
    this.http.get(url).subscribe((data: Cards) => {
      this.singleric = data;
    });
    return this.singleric;
  }

  /*organizzed_ARRAYS(){
    for(let card of this.singleric["cards"]){
      if(card.type == 'main'){
        this.maindeck.push(card);
      }else if(card.type == 'extra'){
        this.extradeck.push(card);
      }else if(card.type == 'side'){
        this.sidedeck.push(card);
      }
    }
    console.log(this.maindeck[0]);
    console.log(this.extradeck[0]);
    console.log(this.sidedeck[0]);
  }*/

  Getallallrecipes(): Recipes{
    let url = environment.url+"/recipes"
    this.http.get(url).subscribe((data: Recipes) => {
      this.allric = data;
    });
    return this.allric;
  }

  Getrecipesidofuser(){
    let url = environment.url+"/recipes/"+localStorage.getItem("token_decoded_userid")+"/user";
    this.http.get(url).subscribe((data: Array<allidrecipe>) => {
      this.recipesid = data;
    });
    //console.log(this.recipesid['recipes'][0].idric);
    //console.log(this.recipesid['recipes'].length);
  }

  ControllRecipe(id: number): number{
    for(let i = 0; i<=this.recipesid['recipes'].length; i++){
      if(this.recipesid['recipes'][i].idric == id){
        return id;
      }else{
        return null;
      }
    }
  }

  Addcomment(id: number, desc: string){
    let url = environment.url+"/comments/user/"+localStorage.getItem("token_decoded_userid")+"/ricetta/"+id
    let comment = {};
    comment["desc"] = desc;
    this.user_comment = {
      nome: this.utente.UserLoggedName,
      cognome: this.utente.UserLoggedCognome,
      id_com: null,
      desc: desc
    }

    this.http.post(url, comment).subscribe((data: any) => {
      console.info(data);
      this.user_comment["id_com"] = data.id
      this.common_id_comment['id_com'] = data.id
    });
    //console.log(this.user_comment)
    this.all_comment_ids["comments"].push(this.common_id_comment);
    this.all_comment["comments"].push(this.user_comment);

    alert("commento pubblicato!")
  }

  Getrecipescomments(id: number){
    let url = environment.url+"/comments/recipe/"+id;
    this.http.get(url).subscribe((data: Comments) => {
      this.all_comment = data;
    });
  }

  Getrecipescommentsids(id: number, iduser: number){
    let url = environment.url+"/comments/recipe/"+ id +"/user/" + iduser;
    this.http.get(url).subscribe((data: allidcomment) => {
      this.all_comment_ids = data;
    });
  }

  DeleteRecipe(idrecipe: number, index: number){
    this.allric["recipes"].splice(index, 1);
    let url = environment.url+"/recipe/"+idrecipe;
    this.http.delete(url).subscribe((data: any) => {
      console.log(data);
    })
  }

  DeleteComment(idcomment: number, index: number){
    this.all_comment["comments"].splice(index, 1);
    let url = environment.url+"/comment/"+idcomment;
    this.http.delete(url).subscribe((data: any) => {
      console.log(data);
    })
  }
}

export interface Recipe{
  idric: number;
  nomeric: string;
  desc: string;
}

export interface Recipes{
  recipes: Array<Recipe>;
}

export interface idrecipe{
  idric: number;
}

export interface idcomment{
  id_com: number;
}

export interface allidrecipe{
  recipes: Array<idrecipe>;
}

export interface allidcomment{
  comments: Array<idcomment>;
}

export interface Comment{
  nome: string;
  cognome: string;
  id_com: number;
  desc: string;
}

export interface Comments{
  comments: Array<Comment>;
}
